.vaccine-app-info {
  color: #333;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #f8f8f8;
  padding-top: 0.9rem;
  box-sizing: border-box;
  overflow-y: auto;
}

.vaccine-app-info .date-picker-list {
  width: 6.86rem;
  margin: 0 auto;
  margin-top: 0.4rem;
}

.vaccine-app-info .info-label {
  display: inline-block;
  width: 1.5rem;
  text-align: right;
  font-size: 0.3rem;
}

.vaccine-app-info .am-list-body {
  background-color: transparent;
  border: 0;
}

.vaccine-app-info .am-list-body::before {
  height: 0!important;
}

.vaccine-app-info .am-list-body::after {
  height: 0!important;
}

.vaccine-app-info .info-content {
  display: inline-block;
  font-size: 0.3rem;
}

.vaccine-app-info .info-content .info-input {
  border: 0.01rem solid #e5e5e5;
  width: 5rem;
  height: 0.68rem;
  margin-left: 0.14rem;
  padding-left: 0.2rem;
  font-size: 0.25rem;
}

.vaccine-app-info .info-content input::-webkit-input-placeholder {
  color: #888;
}

.vaccine-app-info .info-content .am-list-item {
  width: 5.2rem;
  background-clip: content-box;
}

.vaccine-app-info .info-content .am-list-item .am-list-line {
  height: 0.68rem;
  line-height: 0.68rem;
  border: 0.01rem solid #e5e5e5;
}

.vaccine-app-info .info-content .am-list-item .am-list-line .am-list-extra {
  flex-basis: 95%;
  text-align: left;
  padding-left: 0.2rem;
  font-size: 0.25rem;
}

.vaccine-app-info input[type='radio'] {
  display: none;
}

.vaccine-app-info .item {
  display: inline-block;
  width: 0.95rem;
  height: 0.3rem;
  background: url(data:image/png;base64,) no-repeat;
  background-size: 0.3rem 0.3rem;
  vertical-align: middle;
  padding-left: 0.35rem;
  margin-top: -0.1rem;
}

.vaccine-app-info input[type='radio']:checked + .item {
  background: url(data:image/png;base64,) no-repeat;
  background-size: 0.3rem 0.3rem;
}

.vaccine-app-info .ml-20 {
  margin-left: 0.2rem;
}

.vaccine-app-info .btn-submit {
  margin-top: 0.86rem;
  width: 2.4rem;
  height: 0.7rem;
  line-height: 0.7rem;
  font-size: 0.35rem;
}

.vaccine-app-city .info-content .am-list-item {
  margin-top: 0.32rem;
  width: 6.86rem;
  padding-left: 0;
}

.vaccine-app-city .info-content .am-list-item .am-list-line .am-list-extra {
  font-size: 0.3rem;
}

.vaccine-app-city .btn-submit {
  font-size: 0.34rem;
  margin-top: 1.18rem;
}